import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'

import ReposDisplay from '../../components/Custom/ReposDisplay/ReposDisplay'
import Loader from '../../components/Custom/Loader/Loader'

import '../Repos/Repos.style.scss'

const Repos = () => {
    const navigate = useNavigate()
    const { login } = useParams()
    const [ userRepos, setUserRepos ] = useState(null)
    const [ loader, setLoader ] = useState(false)

    useEffect(() => {
        
        const testx = async () => {
            setUserRepos(null)
            setLoader(true)
            const API = `https://api.github.com/users/${login}/repos`
            const response = await fetch(API)
            const result = await response.json()
            setUserRepos(result)
            setLoader(false)
        }

        testx()
    }, [login])

    return (
        <div className='repos flex flex-col justify-evenly'>
            {
                //loader && <Loader/>
            }
            <div className='reposCtn self-center grid'>
                {
                    /*
                    'asdsaf' - para test  repos
                    
                    userRepos?.length > 0 && userRepos?.map( (repo, index) => (
                            <ReposDisplay
                                key={index}
                                name={repo.name}
                                full_name={repo.full_name}
                                html_url={repo.html_url}
                                created_at={repo.created_at}
                                updated_at={repo.updated_at}
                            />
                    ))*/
                    userRepos && userRepos.length > 0 ? (
                        userRepos.map( (repo, index) => (
                            <ReposDisplay
                                key={index}
                                name={repo.name}
                                full_name={repo.full_name}
                                html_url={repo.html_url}
                                created_at={repo.created_at}
                                updated_at={repo.updated_at}
                            />
                        ))
                    ) : (
                        loader ? <Loader/> : <p>no hubo</p> 
                    )
                }
            </div>
            <button 
            className='self-center'
            onClick={() => navigate(-1)}>Go back</button>
        </div>
    )
}

export default Repos
